import React from 'react'
import {useDispatch, useSelector} from 'react-redux'
import { useDaumPostcodePopup } from 'react-daum-postcode'
import { setSearchAddress } from '../../redux/action'

const LayerInfo3 = ({isLayerOpen, isLayerClose}) => {
    const dispatch = useDispatch()
    const choiceAddress = useSelector(state => state.other.searchAddress)

    //    
    const open = useDaumPostcodePopup('//t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js');

    const handleComplete = (data) => {
      let fullAddress = data.address;
      let extraAddress = '';
  
      if (data.addressType === 'R') {
        if (data.bname !== '') {
          extraAddress += data.bname;
        }
        if (data.buildingName !== '') {
          extraAddress += extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName;
        }
        fullAddress += extraAddress !== '' ? ` (${extraAddress})` : '';
      }
  
      console.log(`fullAddress = ${fullAddress}`); // ex)       
      dispatch(setSearchAddress(fullAddress))
    };
  
    const handleClick = () => {
      open({ onComplete: handleComplete })
    }
  
    if(!isLayerOpen) {
        return null // false      _LayerInfo3.jsx  
                    // DeliveryAddress if ' : null'  if   return null 
    }
    return (
        <>
             <div className="layoutBox">
                <div className="inner">
                    <div className="panel">
                        <button onClick={isLayerClose}></button>
                        <div className="cover">
                            <div className="post">
                                <h2> </h2>
                                <div className="search">
                                    <a className="btn_search" onClick={handleClick} href="#!"></a>
                                </div>
                                <div className="list-wrapper">
                                    <div className="info">
                                        <p>''    </p>
                                        <dl>
                                            <dt> </dt>
                                            <dd>{choiceAddress}</dd>
                                        </dl>
                                    </div>
                                </div>
                                <p className="btn-cover">
                                    <a className="btn" onClick={isLayerClose} href="#!"></a>
                                    <a className="btn_ok" onClick={isLayerClose} href="#!"></a>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default LayerInfo3;